import React from 'react';
import { format } from 'date-fns';

export default function CreditBalance({
    balances,
    transactions,
    type,
    label,
    lowThreshold,
    showRecentTransactions,
    addCreditLink,
    allTransactionsLink,
}) {
    const typeBalance = balances[type] || 'NaN';
    const typeTransactions = showRecentTransactions
        ? transactions[type] || []
        : [];
    const lowBalance = parseFloat(typeBalance) < lowThreshold;

    // Create our number formatter.
    const currencyFormatter = new Intl.NumberFormat('en-US', {
        style: 'currency',
        currency: 'USD',
    });

    return (
        <div className="credit-balance">
            <div className="credit-balance__header">{`${label} Balance`}</div>
            <div
                className={`credit-balance__balance ${
                    lowBalance ? 'credit-balance__balance--low' : ''
                }`}
            >
                <div className="credit-balance__balance-amount">
                    {currencyFormatter.format(typeBalance)}
                    {lowBalance && (
                        <div className="credit-balance__balance-amount-low">
                            <svg className="svg-icon svg-icon--small">
                                <title>Warning</title>
                                <use href="#warning" />
                            </svg>
                            Low balance
                        </div>
                    )}
                </div>
                {addCreditLink && (
                    <a
                        href={addCreditLink}
                        className="credit-balance__balance-add"
                    >
                        <svg aria-hidden className="svg-icon svg-icon--small">
                            <use href="#add-alt" />
                        </svg>
                        Add more balance
                    </a>
                )}
            </div>
            {showRecentTransactions && typeTransactions.length > 0 && (
                <div className="credit-balance__transactions">
                    <ul className="credit-balance__transactions-list">
                        {typeTransactions.map(
                            ({ id, label: transactionlabel, amount, date }) => {
                                return (
                                    <li key={id} className="credit-transaction">
                                        <div className="credit-transaction__details">
                                            <div className="credit-transaction__label">
                                                {transactionlabel}
                                            </div>
                                            {date && (
                                                <div className="credit-transaction__date">
                                                    {format(
                                                        date,
                                                        'dd MMM, yyyy, h:mmaaa'
                                                    )}
                                                </div>
                                            )}
                                        </div>
                                        <div className="credit-transaction__amount">
                                            {currencyFormatter.format(amount)}
                                        </div>
                                    </li>
                                );
                            }
                        )}
                    </ul>
                    {allTransactionsLink && (
                        <a
                            href={allTransactionsLink}
                            className="credit-balance__all-transactions"
                        >
                            <svg className="svg-icon svg-icon--small">
                                <title>External link</title>
                                <use href="#external" />
                            </svg>
                            See all transactions
                        </a>
                    )}
                </div>
            )}
        </div>
    );
}
